import React, { createContext, useContext } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { GClosureHistory } from '../../../graph/types'
import { formatDateISO } from '../../../helpers/formats'
import {
  IQueryResult,
  useExecutableQuery,
} from '../../../hooks/useExecutableQuery'
import { ClosureHistoryQuery } from './ClosureHistoryLoader.graphql'

export type TReports = GClosureHistory.FinancialReports

const context = createContext<IQueryResult<GClosureHistory.Query> | null>(null)

export const useClosureHistory = () => useContext(context)!

export const ClosureHistoryLoader: React.SFC = ({ children }) => {
  const { statistics } = useRoot()
  const { result, execute } = useExecutableQuery<
    GClosureHistory.Query,
    GClosureHistory.Variables
  >(ClosureHistoryQuery)

  const onSubmit = () => {
    if (statistics.closureBranch) {
      execute({
        branchId: statistics.closureBranch,
        dateFrom: formatDateISO(statistics.dateFrom),
        dateTo: formatDateISO(statistics.dateTo),
      })
    }
  }

  return (
    <Form
      onSubmit={onSubmit}
      onChange={({ values }) => statistics.updateFilter(values)}
    >
      <context.Provider value={result}>{children}</context.Provider>
    </Form>
  )
}
