import React, { useContext } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { GOrdersStatistics } from '../../../graph/types'
import { createStatisticsQuery } from '../hooks/createStatisticsQuery'
import { OrdersStatisticsQuery } from './OrdersStatisticsLoader.graphql'

export type TOrdersStats = GOrdersStatistics.FinishedOrders

const { context, useStatisticsQuery } = createStatisticsQuery<
  GOrdersStatistics.Query,
  GOrdersStatistics.Variables
>(OrdersStatisticsQuery)

export const useOrderStatistics = () => useContext(context)!

export const OrdersStatisticsLoader: React.FC = ({ children }) => {
  const { statistics } = useRoot()
  const { result, executeFilter } = useStatisticsQuery()

  return (
    <Form
      onSubmit={executeFilter}
      onChange={({ values }) => statistics.updateFilter(values)}
    >
      <context.Provider value={result}>{children}</context.Provider>
    </Form>
  )
}
