import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GClosureReportQuery } from '../../../graph/types'

export const FinancialReport = gql`
  query GClosureReportQuery(
    $branchId: ID!
    $reportId: ID!
    $showReport: Boolean!
  ) {
    lastReport: lastFinancialReport(companyBranchId: $branchId)
      @skip(if: $showReport) {
      plainData
      reportDatetime
      reportSequence
    }
    report: financialReport(report: $reportId) @include(if: $showReport) {
      plainData
      reportDatetime
      reportSequence
    }
  }
`

export const QueryFinancialReport = buildQuery<
  GClosureReportQuery.Query,
  GClosureReportQuery.Variables
>(FinancialReport)
