import { ApolloError, QueryOptions } from 'apollo-client'
import { isApolloError } from 'apollo-client/errors/ApolloError'
import { getOperationName } from 'apollo-utilities'
import { DocumentNode, GraphQLError } from 'graphql'
import { runInAction } from 'mobx'
import { useObservable } from 'mobx-react-lite'

import { useApolloClient } from '../atoms/RootProvider'
import { reportError, reportFailingQuery } from '../helpers/sentry'

export interface IQueryResult<TData> {
  data: Maybe<TData>
  loading: boolean
  errors?: GraphQLError[]
  error?: ApolloError
  hasAnyError: boolean
  wasExecuted: boolean
}

export function useExecutableQuery<TData, TVariables>(
  query: DocumentNode,
  options: Partial<QueryOptions<TVariables>> = {},
) {
  const client = useApolloClient()
  const result: IQueryResult<TData> = useObservable({
    data: null,
    loading: false,
    wasExecuted: false,
    get hasAnyError() {
      return Boolean((result.errors && result.errors.length) || result.error)
    },
  })
  const execute = async (variables: TVariables) => {
    result.loading = true
    try {
      const queryResult = await client.query<TData, TVariables>({
        ...options,
        query,
        variables,
      })
      runInAction(() => {
        result.wasExecuted = true
        result.data = queryResult.data
        result.errors = queryResult.errors
        result.loading = false
      })
    } catch (err) {
      if (isApolloError(err)) {
        runInAction(() => {
          result.error = err
          result.errors = err.graphQLErrors
          result.loading = false
        })
      } else {
        runInAction(() => {
          result.error = err
          result.loading = false
        })
        reportError(err)
      }
    }
    if (result.errors) {
      reportFailingQuery(result.errors, {
        query: getOperationName(query),
        variables,
      })
    }
    return result
  }
  return { result, execute }
}
