import React, { useContext } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { GDailyStatistics } from '../../../graph/types'
import { createStatisticsQuery } from '../hooks/createStatisticsQuery'
import { DailyStatisticsQuery } from './DailyStatisticsLoader.graphql'

export type TOrdersStats = GDailyStatistics.Stats

const { useStatisticsQuery, context } = createStatisticsQuery<
  GDailyStatistics.Query,
  GDailyStatistics.Variables
>(DailyStatisticsQuery)

export const useDailyStatistics = () => useContext(context)!

export const DailyStatisticsLoader: React.SFC = ({ children }) => {
  const { statistics } = useRoot()
  const { result, executeFilter } = useStatisticsQuery()

  return (
    <Form
      onSubmit={executeFilter}
      onChange={({ values }) => statistics.updateFilter(values)}
    >
      <context.Provider value={result}>{children}</context.Provider>
    </Form>
  )
}
