import React from 'react'

import { GCustomer } from '../../../graph/types'

interface ICustomerNameProps {
  customer: GCustomer.Fragment | null
}

export const CustomerIdentity: React.SFC<ICustomerNameProps> = ({ customer }) =>
  customer ? (
    <span>{`${
      customer.lastName
        ? `${customer.firstName} ${customer.lastName}`
        : customer.defaultPhone
          ? customer.defaultPhone.phone
          : ``
    }`}</span>
  ) : (
    <i>unknown</i>
  )
