import { Plural, Trans } from '@lingui/react'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FormInputDate } from '../../../atoms/FormInputDate'
import { useRoot } from '../../../atoms/RootProvider'
import { FormSubmit } from '../../../molecules/FormSubmit'

interface IProps {
  submitButton?: ReactNode
}

export const StatisticsDateRangeFilter = observer<IProps>(
  ({ submitButton }) => {
    const { statistics } = useRoot()

    return (
      <FlexRowSingle justify="flex-start">
        <FlexColumn fixedWidth={true} width="10rem">
          <Trans>From date</Trans>
          <FormInputDate field="dateFrom" defaultValue={statistics.dateFrom} />
        </FlexColumn>
        <FlexColumn fixedWidth={true} width="10rem">
          <Trans>To date</Trans>
          <FormInputDate field="dateTo" defaultValue={statistics.dateTo} />
        </FlexColumn>
        <FlexColumn>
          {submitButton || (
            <FormSubmit width="15rem">
              <Plural
                value={statistics.branchesFilter.length}
                one="Show for one branch"
                few="Show for # branches"
                other="Show for # branches"
              />
            </FormSubmit>
          )}
        </FlexColumn>
      </FlexRowSingle>
    )
  },
)
