import { Trans } from '@lingui/react'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { BulletList } from 'react-content-loader'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { useRoot } from '../../../atoms/RootProvider'
import { OrderStateCategoryEnum } from '../../../graph/types'
import { OrderBox } from '../../../segments/order/atoms/OrderBox'
import { useOrderStatistics } from './OrdersStatisticsLoader'

export const OrdersStatisticsList = observer(() => {
  const { statistics, order: orderModel } = useRoot()
  const { data, loading, wasExecuted } = useOrderStatistics()
  if (!wasExecuted) {
    return null
  }
  if (loading || !data) {
    return <BulletList primaryOpacity={0.2} secondaryOpacity={0.2} />
  }
  const { orders } = data.stats.finishedOrders
  if (orders.length === 0) {
    return (
      <CardListTitle>
        <Trans>No orders</Trans>
      </CardListTitle>
    )
  }
  return (
    <>
      {orders.map(order => (
        <OrderBox
          key={order.id}
          order={{
            ...order,
            orderStateCategory: OrderStateCategoryEnum.Finished,
          }}
          toggleOrderDetail={orderModel.list.toggleOrderDetail}
          isOrderDetailOpen={orderModel.list.isOrderDetailOpen}
          showBranch={statistics.branchesFilter.length > 1}
        />
      ))}
    </>
  )
})
