import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GOrderState } from '../../../graph/types'
import { OrderStateCategoryFragment } from '../models/OrderStateModel.graphql'

export const QueryOrderState = buildQuery<
  GOrderState.Query,
  GOrderState.Variables
>(gql`
  query GOrderState($orderId: ID!, $branchId: ID!) {
    order(id: $orderId) {
      deliveryType {
        enum
      }
      ...GOrderStateCategory
    }
    companyBranch(id: $branchId) {
      id
      allowedCategories
    }
  }
  ${OrderStateCategoryFragment}
`)
