import React from 'react'
import styled from 'styled-components'

import { GOrderDetail } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { OrderWareLine } from './OrderWareLine'

interface IProps {
  order: GOrderDetail.Order
}

const OrderCartTotalsContainer = styled.div`
  margin: 0 0 0 auto;
  width: 20rem;
  text-align: right;
`

export const OrderDetailTotals: React.SFC<IProps> = ({ order }) => (
  <OrderCartTotalsContainer>
    <OrderWareLine
      label={i18n.t`Transport fee`}
      price={order.transportFee.value}
    />
    <OrderWareLine
      label={i18n.t`Covers price`}
      price={order.coverPrice.value}
    />
    <OrderWareLine
      label={i18n.t`Discount total`}
      price={order.totalDiscount.value * -1}
    />
    <b>
      <OrderWareLine label={i18n.t`Total price`} price={order.totalSum.value} />
    </b>
  </OrderCartTotalsContainer>
)
