import { Trans } from '@lingui/react'
import { Timelapse } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { IconLine } from '../../../atoms/IconLine'
import { GOrderDetail } from '../../../graph/types'
import { formatTimeWithoutTodayDate } from '../../../helpers/formats'

interface IProps {
  order: GOrderDetail.Order
}

const LabelStyled = styled.i`
  padding-left: 1rem;
  font-size: 0.8rem;
`

export const OrderStats: React.SFC<IProps> = ({ order }) => {
  const stats: ReactNode[] = []
  if (order.deliverAt) {
    stats.push(
      <div key="deliverAt">
        {formatTimeWithoutTodayDate(order.deliverAt)}
        <LabelStyled>
          <Trans>Deliver on time</Trans>
        </LabelStyled>
      </div>,
    )
  }
  if (order.driver) {
    const { firstName, lastName } = order.driver.user
    stats.push(
      <div key="driver">
        {`${firstName} ${lastName}`}
        <LabelStyled>
          <Trans>Driver</Trans>
        </LabelStyled>
      </div>,
    )
  }
  if (stats.length === 0) {
    return null
  }
  return (
    <IconLine icon={Timelapse} labelMargin="0">
      {stats}
    </IconLine>
  )
}
