import React from 'react'

import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { WithRoot } from '../../../atoms/WithRoot'
import { i18n } from '../../../i18n'
import { QueryFinancialReport } from './ClosureStatisticsContent.graphql'

interface IProps {
  reportId?: string
}

export const ClosureStatisticsContent: React.SFC<IProps> = ({
  reportId = '',
}) => (
  <WithRoot
    render={({ statistics }) =>
      statistics.closureBranch ? (
        <QueryFinancialReport
          variables={{
            branchId: statistics.closureBranch,
            reportId,
            showReport: Boolean(reportId),
          }}
          fetchPolicy="cache-and-network"
          renderLoading={false}
          render={({ report, lastReport }, { loading }) =>
            loading ? (
              <MonospaceOutput text={i18n.t`Computing`} />
            ) : (
              <MonospaceOutput
                text={reportId ? report!.plainData : lastReport.plainData}
              />
            )
          }
        />
      ) : null
    }
  />
)
