import { DocumentNode } from 'graphql'
import { createContext } from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { formatDateISO } from '../../../helpers/formats'
import {
  IQueryResult,
  useExecutableQuery,
} from '../../../hooks/useExecutableQuery'

interface IVariables {
  readonly branches: ReadonlyArray<string>
  readonly dateFrom: string
  readonly dateTo: string
}

export function createStatisticsQuery<TData, TVariables extends IVariables>(
  query: DocumentNode,
) {
  const context = createContext<IQueryResult<TData>>({
    data: null,
    hasAnyError: false,
    loading: false,
    wasExecuted: false,
  })
  const useStatisticsQuery = () => {
    const { statistics } = useRoot()
    const { result, execute } = useExecutableQuery<TData, IVariables>(query)
    const executeFilter = () => {
      execute({
        branches: statistics.branchesFilter,
        dateFrom: formatDateISO(statistics.dateFrom),
        dateTo: formatDateISO(statistics.dateTo),
      })
    }
    return { result, executeFilter }
  }
  return {
    context,
    useStatisticsQuery,
  }
}
