import { CancelOutlined } from '@material-ui/icons'
import { differenceInMinutes } from 'date-fns'
import { css } from 'emotion'
import React from 'react'

import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import { DateTime } from '../../../graph/types'
import { MINUTE, useTimer } from '../../../hooks/useTimer'

interface IDeliveryTimeBadgeProps {
  deliverAt: Maybe<DateTime>
  deliveredAt: Maybe<DateTime>
  isCancelled?: boolean
  onClick?(): void
}

const cancelIconPadding = css`
  padding: 0.2rem 0.3rem 0.1rem 0.3rem;
`

export const DeliveryTimeBadge: React.SFC<IDeliveryTimeBadgeProps> = ({
  deliverAt,
  deliveredAt,
  isCancelled = false,
  onClick,
}) => {
  const timerEnabled = !(deliveredAt || isCancelled)
  const now = useTimer({ interval: MINUTE, enabled: timerEnabled })

  let variant = ETimeBadgeVariant.UNKNOWN

  if (isCancelled) {
    return (
      <TimeBadge variant={variant} className={cancelIconPadding}>
        <CancelOutlined />
      </TimeBadge>
    )
  }

  if (!deliverAt) {
    return <TimeBadge variant={variant}></TimeBadge>
  }

  const minutes = differenceInMinutes(deliverAt, deliveredAt || now)

  if (minutes > 20) {
    variant = ETimeBadgeVariant.NORMAL
  } else if (minutes > 0) {
    variant = ETimeBadgeVariant.WARNING
  } else {
    variant = ETimeBadgeVariant.CRITICAL
  }

  return <TimeBadge minutes={minutes} variant={variant} onClick={onClick} />
}
