import React from 'react'

import { OrderStateCategoryEnum } from '../../../graph/types'
import { OrderDriverSelect } from '../atoms/OrderDriverSelect'
import { TOrderStateModel } from '../models/OrderStateModel'
import { QueryDriverSelection } from './OrderStateDeliver.graphql'

interface IProps {
  branchId: ID
  orderId: ID
  orderStateCategory: OrderStateCategoryEnum
  stateModel: TOrderStateModel
}

export const OrderStateDeliver: React.SFC<IProps> = ({
  branchId,
  orderId,
  orderStateCategory,
  stateModel,
}) => (
  <QueryDriverSelection
    fetchPolicy="network-only"
    variables={{ branchId: branchId, orderId }}
    render={(
      { drivers, order: { driver: orderDriver } },
      { loading, refetch },
    ) => (
      <OrderDriverSelect
        drivers={drivers}
        selectedDriver={orderDriver ? orderDriver.user : null}
        isLoading={loading}
        onReload={refetch}
        onClear={() => {
          if (orderDriver) stateModel.removeDriver(orderDriver.id)
        }}
        onSelect={driver => {
          stateModel.selectDriver(orderId, driver.id)
          if (orderStateCategory !== OrderStateCategoryEnum.OnWay) {
            stateModel.deliverOrder(orderId)
          }
        }}
      />
    )}
  />
)
