import { Trans } from '@lingui/react'
import Color from 'color'
import React from 'react'
import { Element } from 'react-scroll'
import styled from 'styled-components'

import { Price } from '../../../atoms/Price'
import {
  GOrderListTicker,
  GOrderStateCategory,
  OrderStateCategoryEnum,
} from '../../../graph/types'
import { formatAddress } from '../../../helpers/formats'
import { Colors, Radiuses } from '../../../styling'
import { CustomerIdentity } from '../atoms/CustomerIdentity'
import { DeliveryTimeBadge } from '../atoms/DeliveryTimeBadge'

interface IOrderTickerProps {
  onClick(): void
  order: GOrderListTicker.Fragment & GOrderStateCategory.Fragment
  showBranch: boolean
}

const OrderTickerStyled = styled(Element)`
  text-align: left;
  cursor: pointer;
  &:hover {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.05)
      .string()};
  }
  &:active {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.2)
      .string()};
  }
`

const OrderTickerMainContentStyled = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0.4rem;
`

const DeliveryTimeBadgeContainerStyled = styled.div`
  flex: 0 0 2rem;
`
const AddressDisplayContainerStyled = styled.div`
  flex: 1 0;
  padding: 0 0 0 0.5rem;
`
const CustomerIdentityContainerStyled = styled.div`
  flex: 1 0;
  padding: 0 0 0 0.5rem;
`
const PriceContainerStyled = styled.div`
  flex: 0 0 3.5rem;
  text-align: right;
  position: relative;
`
const PaidMarkStyled = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  &:after {
    content: '';
    position: absolute;
    top: 0.1rem;
    right: -0.3rem;
    left: 0.9rem;
    bottom: 0.3rem;
    border-top: 2px solid ${Colors.orange};
    transform: rotate(13deg);
    transform-origin: 0% 0%;
    opacity: 0.7;
  }
`
const PaidTextStyled = styled.div`
  color: ${Colors.orange};
`

const ToBranchContainerStyled = styled.div`
  padding: 0.3rem 0.6rem;
  display: inline-block;
  border-top-left-radius: ${Radiuses.Card};
  background-color: ${Colors.green};
  color: ${Colors.white};
`

export const OrderTicker: React.SFC<IOrderTickerProps> = ({
  order,
  showBranch,
  onClick,
}) => (
  <OrderTickerStyled onClick={onClick} name={`order-${order.id}`}>
    {showBranch && (
      <ToBranchContainerStyled>
        {order.companyBranch.name}
      </ToBranchContainerStyled>
    )}

    <OrderTickerMainContentStyled>
      <DeliveryTimeBadgeContainerStyled>
        <DeliveryTimeBadge
          deliverAt={order.deliverAt}
          deliveredAt={order.deliveredAt}
          isCancelled={
            order.orderStateCategory === OrderStateCategoryEnum.Cancel
          }
        />
      </DeliveryTimeBadgeContainerStyled>

      <AddressDisplayContainerStyled>
        {formatAddress(order.address)}
      </AddressDisplayContainerStyled>

      <CustomerIdentityContainerStyled>
        <CustomerIdentity customer={order.customer} />
      </CustomerIdentityContainerStyled>

      <PriceContainerStyled>
        <Price price={order.totalSum.value} />
        {order.isPaidWithCard && (
          <>
            <PaidMarkStyled />
            <PaidTextStyled>
              <Trans>Paid</Trans>
            </PaidTextStyled>
          </>
        )}
      </PriceContainerStyled>
    </OrderTickerMainContentStyled>
  </OrderTickerStyled>
)
