import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { DeliveryTimeDialog } from '../../../molecules/DeliveryTimeDialog'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  orderId: ID
}

export const OrderStateTimeNotification: React.FC<IProps> = ({ orderId }) => {
  const { order } = useRoot()

  return (
    <DeliveryTimeDialog
      title={i18n.t`Send expected time of delivery to customer (in minutes)`}
      onConfirm={minutes => {
        order.state.sendTimeNotification(orderId, minutes)
      }}
    >
      {({ openDialog }) => (
        <TouchButton iconName="schedule" onClick={openDialog} />
      )}
    </DeliveryTimeDialog>
  )
}
