import { Trans } from '@lingui/react'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { Headline } from '../../../atoms/Headline'
import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { i18n } from '../../../i18n'
import { useDailyStatistics } from './DailyStatisticsLoader'

export const DailyStatisticsContent = observer(() => {
  const { data, loading } = useDailyStatistics()
  if (!data) {
    return null
  }
  if (loading) {
    return <MonospaceOutput text={i18n.t`Computing`} />
  }
  const { stats } = data
  return (
    <>
      <Headline light={true}>
        <Trans>Daily statistics</Trans>
      </Headline>
      <MonospaceOutput text={stats.dailyStatisticsPlain} />

      <Headline light={true}>
        <Trans>By category</Trans>
      </Headline>
      <MonospaceOutput text={stats.categoryStatisticsPlain} />

      <Headline light={true}>
        <Trans>By type</Trans>
      </Headline>
      <MonospaceOutput text={stats.deliveryStatisticsPlain} />

      <Headline light={true}>
        <Trans>By driver</Trans>
      </Headline>
      <MonospaceOutput text={stats.driversStatisticsPlain} />
    </>
  )
})
