import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import {
  EmailFragment,
  PhoneFragment,
  PriceFragment,
} from '../../../graph/fragments'
import { GOrderDetail } from '../../../graph/types'
import { OrderDriverFragment } from '../molecules/OrderStateDeliver.graphql'

export const QueryOrderDetail = buildQuery<
  GOrderDetail.Query,
  GOrderDetail.Variables
>(gql`
  query GOrderDetail($id: ID!) {
    order(id: $id) {
      id
      createdAt
      deliverAt
      companyBranch {
        id
      }
      customer {
        emails {
          ...GEmail
        }
        phones {
          ...GPhone
        }
      }
      customerOrdersCountCurrent
      customerOrdersCountTotal
      note
      previousNote
      allAppliedRules
      items {
        ...GOrderDetailWare
        sidedishes: sideDishes {
          ...GOrderDetailWare
        }
      }
      totalSum {
        ...GPrice
      }
      transportFee {
        ...GPrice
      }
      coverPrice {
        ...GPrice
      }
      totalDiscount {
        ...GPrice
      }
      orderTiming: orderStateCategoryRelations {
        createdAt
        orderStateCategory
      }
      ...GOrderDriver
    }
  }

  fragment GOrderDetailWare on OrderRecipe {
    id
    recipe {
      id
      code
      nameLabel
    }
    price {
      ...GPrice
    }
  }
  ${PriceFragment}
  ${EmailFragment}
  ${PhoneFragment}
  ${OrderDriverFragment}
`)
