import gql from 'graphql-tag'

import { AddressFragment, CustomerFragment } from '../../../graph/fragments'

export const OrderTickerFragment = gql`
  fragment GOrderListTicker on Order {
    id
    createdAt
    isPaidWithCard
    deliverAt
    deliverAtWorst
    deliveredAt
    companyBranch {
      id
      name
      oldOrderThreshold
    }
    address {
      ...GAddress
    }
    customer {
      ...GCustomer
    }
    totalSum {
      value
    }
  }
  ${AddressFragment}
  ${CustomerFragment}
`
