import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { buildSubscription } from '../../../graph/buildSubscription'
import { GOrderList, SOrderList } from '../../../graph/types'
import { OrderStateCategoryFragment } from '../models/OrderStateModel.graphql'
import { OrderMapMarkerFragment } from '../molecules/OrderMapMarker.graphql'
import { OrderTickerFragment } from '../molecules/OrderTicker.graphql'

const OrderListQuery = gql`
  query GOrderList(
    $branches: [ID!]
    $categories: [OrderStateCategoryEnum!]
    $includeOldFinished: Boolean
  ) {
    orders(
      order: {
        companyBranches: $branches
        orderStateCategoriesOR: $categories
        includeOldFinished: $includeOldFinished
      }
    ) {
      id
      ...GOrderStateCategory
      ...GOrderListTicker
      ...GOrderMapMarker
    }
    orderStateCategories {
      code
      enum
    }
  }

  ${OrderStateCategoryFragment}
  ${OrderMapMarkerFragment}
  ${OrderTickerFragment}
`

const OrderListSubscription = gql`
  subscription SOrderList(
    $branches: [ID!]
    $categories: [OrderStateCategoryEnum!]
  ) {
    updatedOrder(
      companyBranches: $branches
      orderStateCategoriesOR: $categories
    ) {
      order {
        id
        ...GOrderStateCategory
        ...GOrderListTicker
        ...GOrderMapMarker
      }
    }
  }
  ${OrderStateCategoryFragment}
  ${OrderMapMarkerFragment}
  ${OrderTickerFragment}
`

export const QueryOrderList = buildQuery<
  GOrderList.Query,
  GOrderList.Variables
>(OrderListQuery)

export const SubscribeOrderList = buildSubscription<
  SOrderList.Subscription,
  SOrderList.Variables,
  GOrderList.Query
>(OrderListSubscription)
