import { differenceInHours } from 'date-fns'
import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import {
  GOrderList,
  OrderStateCategoryEnum,
  SOrderList,
} from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { QueryOrderList, SubscribeOrderList } from './OrderListLoader.graphql'

const knownCategories = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.ReadyToDeliver,
  OrderStateCategoryEnum.OnWay,
  OrderStateCategoryEnum.Finished,
]

export type TOrder = GOrderList.Orders

export type TCategorizedOrders = { [state in OrderStateCategoryEnum]: TOrder[] }

export interface ILoaderProps {
  isLoading: boolean
  noFilter: boolean
  orders?: ReadonlyArray<TOrder>
  categorizedOrders?: TCategorizedOrders
  categories?: ReadonlyArray<GOrderList.OrderStateCategories>
}

interface IProps {
  children(props: ILoaderProps): ReactNode
  includeOldFinished?: boolean
}

export const OrderListLoader: React.SFC<IProps> = ({
  children,
  includeOldFinished = false,
}) => (
  <WithRoot
    render={root => {
      if (!root.order.list.hasBranchFilter) {
        return children({ isLoading: false, noFilter: true })
      }
      const variables: GOrderList.Variables = {
        branches: root.order.list.branchesFilter,
        categories: knownCategories,
        includeOldFinished,
      }
      return (
        <QueryOrderList
          fetchPolicy="cache-and-network"
          refetchOnConnectionLost={true}
          variables={variables}
          renderLoading={false}
          render={(
            { orders = [], orderStateCategories: categories = [] },
            result,
          ) => {
            return (
              <SubscribeOrderList
                queryResult={result}
                variables={variables}
                handleUpdate={handleSubscriptionUpdate}
              >
                {children({
                  isLoading: result.loading,
                  noFilter: false,
                  orders,
                  categorizedOrders: categorizeOrders(orders),
                  categories: categories.filter(category =>
                    knownCategories.includes(category.enum),
                  ),
                })}
              </SubscribeOrderList>
            )
          }}
        />
      )
    }}
  />
)

export function getOrderState(order: {
  orderStateCategory: OrderStateCategoryEnum
}) {
  return order.orderStateCategory
}

function categorizeOrders(orders: ReadonlyArray<TOrder>) {
  return orders.reduce(
    (result, order) => {
      const category = getOrderState(order)
      if (!knownCategories.includes(category)) {
        return result
      }
      if (!result[category]) {
        result[category] = [order]
      } else {
        result[category].push(order)
      }
      return result
    },
    {} as TCategorizedOrders,
  )
}

function handleSubscriptionUpdate(
  update: SOrderList.Subscription,
  prev: GOrderList.Query,
) {
  const { order } = update.updatedOrder
  if (!order) {
    return prev
  }
  let orderExists = false
  const orders = RM.reject(isOldFinishedOrder, prev.orders).map(o => {
    if (o.id === order.id) {
      orderExists = true
      return order
    }
    return o
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { ...prev, orders }
}

function isOldFinishedOrder(order: GOrderList.Orders) {
  return (
    order.orderStateCategory === OrderStateCategoryEnum.Finished &&
    differenceInHours(new Date(), order.createdAt) >
      order.companyBranch.oldOrderThreshold
  )
}
