import { Observer } from 'mobx-react'
import React from 'react'

interface IObserverWhenProps {
  test(): boolean
  renderSuccess(): ReactNode
  renderFailure?(): ReactNode
}

export const ObserverWhen: React.SFC<IObserverWhenProps> = ({
  test,
  renderSuccess,
  renderFailure,
}) => (
  <Observer
    render={() =>
      test() ? renderSuccess() : renderFailure ? renderFailure() : null
    }
  />
)
