import gql from 'graphql-tag'

import { OrderTickerFragment } from '../../../segments/order/molecules/OrderTicker.graphql'
import { OrderStateCategoryFragment } from '../../order/models/OrderStateModel.graphql'

export const OrdersStatisticsQuery = gql`
  query GOrdersStatistics($branches: [ID!]!, $dateFrom: Date!, $dateTo: Date!) {
    stats: statistics(
      branches: $branches
      dateFrom: $dateFrom
      dateTo: $dateTo
    ) {
      finishedOrders {
        fees {
          ...GStatsPrice
        }
        onlinePayments {
          ...GStatsPrice
        }
        total {
          ...GStatsPrice
        }
        ordersCount
        orders {
          ...GOrderListTicker
          ...GOrderStateCategory
        }
      }
    }
  }

  fragment GStatsPrice on PriceConverted {
    value
    currency {
      code
    }
  }

  ${OrderTickerFragment}
  ${OrderStateCategoryFragment}
`
