import gql from 'graphql-tag'

export const ClosureHistoryQuery = gql`
  query GClosureHistory($branchId: ID!, $dateFrom: Date!, $dateTo: Date!) {
    financialReports(
      companyBranchId: $branchId
      dateFrom: $dateFrom
      dateTo: $dateTo
    ) {
      id
      reportDatetime
      reportSequence
    }
  }
`
