import { Trans } from '@lingui/react'
import { isApolloError } from 'apollo-client/errors/ApolloError'
import { DocumentNode, GraphQLError } from 'graphql'
import Raven from 'raven-js'
import React, { useCallback } from 'react'
import { MutationHookOptions, useMutation } from 'react-apollo-hooks'

import { RM } from '../helpers/ramda'
import { reportError } from '../helpers/sentry'
import { i18n } from '../i18n'
import { TouchButton } from '../molecules/TouchButton'
import { useSnackBar } from './useSnackBar'

interface IGuardedMutationOptions<TData, TVariables>
  extends MutationHookOptions<TData, TVariables> {
  getUserErrors?(data: TData): ReadonlyArray<string> | null
}

export function useGuardedMutation<TData, TVariables>(
  mutation: DocumentNode,
  options: IGuardedMutationOptions<TData, TVariables> = {},
) {
  const defaultMessage = i18n.t`Unexpected error occured while sending a request. Our team was notified about the issue.`

  const { enqueueSnackbar, enqueueError, enqueueInfo } = useSnackBar()

  const handleUserErrors = useCallback((userErrors: ReadonlyArray<string>) => {
    userErrors.forEach(enqueueInfo)
  }, [])

  const handleGraphQLErrors = useCallback(
    (errors: ReadonlyArray<GraphQLError>) => {
      const userErrors = errors.filter(RM.propEq('showToUser', true))
      if (userErrors.length === 0) {
        enqueueError(defaultMessage)
      } else {
        handleUserErrors(userErrors.map(RM.prop('message')))
      }
    },
    [],
  )

  const handleError = useCallback((err: Error) => {
    if (isApolloError(err)) {
      if (err.networkError) {
        enqueueError(
          i18n.t`Communication with server has failed. Please retry the operation in a moment again.`,
        )
        return
      } else if (err.graphQLErrors) {
        handleGraphQLErrors(err.graphQLErrors)
        return
      }
    }
    reportError(err)
    enqueueSnackbar(defaultMessage, {
      action: [<ShowFeedbackButton key="feedback" />],
    })
  }, [])

  const mutate = useMutation(mutation, options)
  return async (variables: TVariables) => {
    try {
      const result = await mutate({ variables })
      if (result.errors) {
        handleGraphQLErrors(result.errors)
        return null
      }
      if (!result.data) {
        return null
      }
      const data = result.data as TData
      if (options.getUserErrors) {
        const userErrors = options.getUserErrors(data)
        if (userErrors !== null) {
          handleUserErrors(userErrors)
        }
      }
      return data
    } catch (err) {
      handleError(err)
      return null
    }
  }
}

function ShowFeedbackButton() {
  return (
    <TouchButton onClick={() => Raven.showReportDialog()}>
      <Trans> Submit feedback </Trans>
    </TouchButton>
  )
}
