import React from 'react'
import styled from 'styled-components'

import { ObserverWhen } from '../../../atoms/ObserverWhen'
import { GOrderListTicker, GOrderStateCategory } from '../../../graph/types'
import { Colors, Radiuses } from '../../../styling'
import { OrderTicker } from '../molecules/OrderTicker'
import { OrderDetail } from '../organisms/OrderDetail'

type TOrder = GOrderListTicker.Fragment & GOrderStateCategory.Fragment

interface IProps {
  order: TOrder
  showBranch: boolean
  toggleOrderDetail(order: TOrder): void
  isOrderDetailOpen(orderId: string): boolean
}

export const OrderBoxStyled = styled.div`
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: 0.5rem;
  position: relative;
`

export const OrderBox: React.SFC<IProps> = ({
  order,
  showBranch,
  toggleOrderDetail,
  isOrderDetailOpen,
}) => (
  <OrderBoxStyled id={`order-${order.id}`}>
    <OrderTicker
      order={order}
      showBranch={showBranch}
      onClick={() => toggleOrderDetail(order)}
    />
    <ObserverWhen
      test={() => isOrderDetailOpen(order.id)}
      renderSuccess={() => <OrderDetail orderId={order.id} />}
    />
  </OrderBoxStyled>
)
