import { i18nMark } from '@lingui/react'
import React from 'react'
import styled from 'styled-components'

import { IconLine } from '../../../atoms/IconLine'
import { GOrderDetail, OrderStateCategoryEnum } from '../../../graph/types'
import { formatTimeWithoutTodayDate } from '../../../helpers/formats'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'

interface IProps {
  orderTiming: ReadonlyArray<GOrderDetail.OrderTiming>
}

const LabelStyled = styled.i`
  padding-left: 1rem;
  font-size: 0.8rem;
`

const labelMap = {
  [OrderStateCategoryEnum.NewOrders]: i18nMark('Created'),
  [OrderStateCategoryEnum.Preparing]: i18nMark('Preparing'),
  [OrderStateCategoryEnum.ReadyToDeliver]: i18nMark('Ready to deliver'),
  [OrderStateCategoryEnum.OnWay]: i18nMark('On way'),
  [OrderStateCategoryEnum.Finished]: i18nMark('Finished'),
}

export const OrderTimeLine: React.SFC<IProps> = ({ orderTiming }) => (
  <IconLine icon="access_time" labelMargin="0">
    {RM.reject(
      ({ createdAt, orderStateCategory }) =>
        createdAt === null || !labelMap[orderStateCategory],
      orderTiming,
    ).map(timing => (
      <div key={timing.orderStateCategory}>
        {formatTimeWithoutTodayDate(timing.createdAt)}
        <LabelStyled>{i18n._(labelMap[timing.orderStateCategory])}</LabelStyled>
      </div>
    ))}
  </IconLine>
)
