import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'

interface IProps {
  onMinuteClick(minute: number): void
}

const minutesOption = [20, 30, 45, 60, 75, 90, 105, 120]

const MinutesContainer = styled.div`
  display: flex;
  justify-content: 'center';
  flex-wrap: 'wrap';
  font-size: 2rem;
`
const MinutesItem = styled.div`
  margin: 1rem 1rem 0 0;
`

export const DeliveryMinutes: React.FC<IProps> = ({ onMinuteClick }) => {
  return (
    <MinutesContainer>
      {minutesOption.map(minutes => (
        <MinutesItem key={minutes}>
          <TouchButton
            onClick={() => {
              onMinuteClick(minutes)
            }}
            color={Colors.green}
          >
            {minutes}
          </TouchButton>
        </MinutesItem>
      ))}
    </MinutesContainer>
  )
}
