import { Trans } from '@lingui/react'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRow } from '../../../atoms/FlexRow'
import { Headline } from '../../../atoms/Headline'
import { Loading } from '../../../atoms/Loading'
import { RelativeLink } from '../../../atoms/RelativeLink'
import { useRoot } from '../../../atoms/RootProvider'
import { formatDateISO } from '../../../helpers/formats'
import { TouchButton } from '../../../molecules/TouchButton'
import { Spacing } from '../../../styling'
import { useClosureHistory } from '../organisms/ClosureHistoryLoader'

export const ClosureHistoryTable = observer(() => {
  const { data, loading } = useClosureHistory()
  const { statistics } = useRoot()
  if (loading) {
    return (
      <Headline>
        <Loading />
      </Headline>
    )
  }
  if (!statistics.closureBranch) {
    return (
      <Headline>
        <Trans>Select a branch</Trans>
      </Headline>
    )
  }
  if (!data || data.financialReports.length === 0) {
    return (
      <Headline>
        <Trans>No closures found</Trans>
      </Headline>
    )
  }
  return (
    <>
      {data.financialReports.map(report => (
        <FlexRow key={report.id}>
          <FlexColumn fixedWidth={true}>
            <RelativeLink to={`../${report.id}`}>
              <TouchButton
                iconName="mode_edit"
                width={Spacing.ButtonHeight}
                height={Spacing.ButtonHeight}
              />
            </RelativeLink>
          </FlexColumn>
          <FlexColumn widthGrowIndex={1}>
            {`[${report.reportSequence}] ${formatDateISO(
              report.reportDatetime,
            )}`}
          </FlexColumn>
        </FlexRow>
      ))}
    </>
  )
})
